import React, { Component } from "react";
import Header from "../../components/header/Header";
import Agiledef from "../../containers/agiledef/Agiledef";
import Agile from "../../containers/agile/Agile";
import Footer from "../../components/footer/Footer";
import TopButton from "../../components/topButton/TopButton";

class AgileClass extends Component {
  render() {
    console.log(this.props.theme);
    return (
      <div>
        <Header theme={this.props.theme} />
        <Agiledef theme={this.props.theme} />
        <Agile theme={this.props.theme} />
        <Footer theme={this.props.theme} />
        <TopButton theme={this.props.theme} />
      </div>
    );
  }
}

export default AgileClass;
