import React, { Component } from "react";
import "./Agile.css";
import SoftwareSkill from "../../components/softwareSkills/SoftwareSkill";
import { agiledef } from "../../portfolio";
import { Fade } from "react-reveal";
import Kanban from "./kanban";
import Manifest from "./manifest";
import Agile from "./agileimage";
import Scrum from "./scrum";

function GetSkillSvg(props) {
  if (props.fileName === "kanban")
    return <Kanban theme={props.theme} />;
  else if (props.fileName === "scrum")
    return <Scrum theme={props.theme} />;
  else if (props.fileName === "agileimage")
    return <Agile theme={props.theme} />;
  return <Manifest theme={props.theme} />;
}

class AgileSection extends Component {
  render() {
    const theme = this.props.theme;
    return (
      <div>
        {agiledef.data.map((skill) => {
          return (
            <div className="skills-main-div">
              <Fade left duration={2000}>
                <div className="skills-image-div">
                  {/* <img
                    alt="Ashutosh is Analysing Data"
                    src={require(`../../assests/images/${skill.imagePath}`)}
                  ></img> */}
                  <GetSkillSvg fileName={skill.fileName} theme={theme} />
                </div>
              </Fade>

              <div className="skills-text-div">
                <Fade right duration={1000}>
                  <h1 className="skills-heading" style={{ color: theme.text }}>
                    {skill.title}
                  </h1>
                </Fade>
                <Fade right duration={1500}>
                  <SoftwareSkill logos={skill.softwareSkills} />
                </Fade>
                <Fade right duration={2000}>
                  <div>
                    {skill.skills.map((skillSentence) => {
                      return (
                        <p
                          className="subTitle skills-text"
                          style={{ color: theme.secondaryText }}
                        >
                          {skillSentence}
                        </p>
                      );
                    })}
                  </div>
                </Fade>
              </div>
            </div>
          );
        })}
      </div>
    );
  }
}

export default AgileSection;
