import React, { Component } from "react";
import Header from "../../components/header/Header";
import Datasciencedef from "../../containers/datasciencedef/Datasciencedef";
import DataScience from "../../containers/datascience/DataScience";
import Footer from "../../components/footer/Footer";
import TopButton from "../../components/topButton/TopButton";

class DataScienceClass extends Component {
  render() {
    console.log(this.props.theme);
    return (
      <div>
        <Header theme={this.props.theme} />
        <Datasciencedef theme={this.props.theme} />
        <DataScience theme={this.props.theme} />
        <Footer theme={this.props.theme} />
        <TopButton theme={this.props.theme} />
      </div>
    );
  }
}

export default DataScienceClass;
