import React, { Component } from "react";
import "./DataScience.css";
import SoftwareSkill from "../../components/softwareSkills/SoftwareSkill";
import { datasciencedef } from "../../portfolio";
import { Fade } from "react-reveal";
import Meeting from "./meeting";
import Ai from "./ai";
import Planning from "./planning";
import Other from "./Other";


function GetSkillSvg(props) {
  if (props.fileName === "meeting")
    return <Meeting theme={props.theme} />;
  else if (props.fileName === "ai")
    return <Ai theme={props.theme} />;
  else if (props.fileName === "planning")
    return <Planning theme={props.theme} />;
  return < Other theme={props.theme} />;
}

class DataScienceSection extends Component {
  render() {
    const theme = this.props.theme;
    return (
      <div>
        {datasciencedef.data.map((skill) => {
          return (
            <div className="skills-main-div">
              <Fade left duration={2000}>
                <div className="skills-image-div">
                  {/* <img
                    alt="Ashutosh is Analysing Data"
                    src={require(`../../assests/images/${skill.imagePath}`)}
                  ></img> */}
                  <GetSkillSvg fileName={skill.fileName} theme={theme} />
                </div>
              </Fade>

              <div className="skills-text-div">
                <Fade right duration={1000}>
                  <h1 className="skills-heading" style={{ color: theme.text }}>
                    {skill.title}
                  </h1>
                </Fade>
                <Fade right duration={1500}>
                  <SoftwareSkill logos={skill.softwareSkills} />
                </Fade>
                <Fade right duration={2000}>
                  <div>
                    {skill.skills.map((skillSentence) => {
                      return (
                        <p
                          className="subTitle skills-text"
                          style={{ color: theme.secondaryText }}
                        >
                          {skillSentence}
                        </p>
                      );
                    })}
                  </div>
                </Fade>
              </div>
            </div>
          );
        })}
      </div>
    );
  }
}

export default DataScienceSection;
